import React from 'react';
import { Link } from 'gatsby';
import Layout from '../components/layout';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';

const BlogTitle = styled('span')(({ theme }) => ({
  fontSize: '1.8rem',
  color: '#66ff66', // キャラクターの緑色
}));

const BlogDate = styled('span')(({ theme }) => ({
  fontSize: '0.875rem',
  color: 'gray',
}));

const CharacterImage = styled('img')(({ theme }) => ({
  width: '25%',
  height: 'auto',
  [theme.breakpoints.up('sm')]: {
    width: 'auto',
    height: '150px',
  },
}));

const PaginationContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  justifyContent: 'space-between',
  marginTop: theme.spacing(4),
}));

type PageContext = {
  posts: {
    node: {
      id: string;
      fields: {
        slug: string;
      };
      frontmatter: {
        title: string;
        date: string;
      };
      excerpt: string;
    };
  }[];
  numPages: number;
  currentPage: number;
};

type Props = {
  pageContext: PageContext;
};

const BlogListTemplate: React.FC<Props> = ({ pageContext }) => {
  const { posts, currentPage, numPages } = pageContext;
  const isFirst = currentPage === 1;
  const isLast = currentPage === numPages;
  const prevPage = currentPage - 1 === 1 ? '/' : `/page/${currentPage - 1}`;
  const nextPage = `/page/${currentPage + 1}`;

  return (
    <Layout>
      <Box sx={{ 
        display: 'flex', 
        alignItems: 'center', 
        gap: 2, 
        mb: 2,
        flexDirection: { xs: 'column', sm: 'row' },
      }}>
        <Typography 
          variant="h1" 
          sx={{
            fontFamily: 'Courier, monospace',
            fontSize: { xs: '2.8rem', sm: '5rem' },
            mb: 0,
            textAlign: { xs: 'center', sm: 'left' },
          }}
        >
          Chiyoji's Page
        </Typography>
        <CharacterImage src="https://pub-01fefff4e3b240f089003a1627fe087d.r2.dev/second_waltz_chara.gif" alt="Second Waltz Character" />
      </Box>
      <List>
        {posts.map(({ node }) => (
          <ListItem key={node.id} button component={Link} to={node.fields.slug}>
            <ListItemText
              primary={<BlogTitle>{node.frontmatter.title}</BlogTitle>}
              secondary={
                <span>
                  <BlogDate>{node.frontmatter.date}</BlogDate>
                  <br />
                  {node.excerpt}
                </span>
              }
            />
          </ListItem>
        ))}
      </List>

      <PaginationContainer>
        {!isFirst && (
          <Button 
            component={Link} 
            to={prevPage} 
            variant="contained"
            sx={{ 
              backgroundColor: '#FFB6C1', 
              color: '#333',
              '&:hover': { backgroundColor: '#f7a7b6' }
            }}
          >
            前へ
          </Button>
        )}
        <Box sx={{ flexGrow: 1 }} />
        {!isLast && (
          <Button 
            component={Link} 
            to={nextPage} 
            variant="contained"
            sx={{ 
              backgroundColor: '#FFB6C1', 
              color: '#333',
              '&:hover': { backgroundColor: '#f7a7b6' }
            }}
          >
            次へ
          </Button>
        )}
      </PaginationContainer>
    </Layout>
  );
};

export default BlogListTemplate;
